import { Dialog, showDialog } from "@jupyterlab/apputils";
import * as React from "react";
/**
 * A component for an HTML form that allows the user
 * to select Dialog parameters.
 */
export class NewCluster extends React.Component {
    /**
     * Construct a new NewCluster component.
     */
    constructor(props) {
        super(props);
        let model;
        model = props.initialModel;
        this.state = { model };
    }
    /**
     * When the component updates we take the opportunity to write
     * the state of the cluster to an external object so this can
     * be sent as the result of the dialog.
     */
    componentDidUpdate() {
        let model = Object.assign({}, this.state.model);
        this.props.stateEscapeHatch(model);
    }
    /**
     * React to the number of workers changing.
     */
    onScaleChanged(event) {
        this.setState({
            model: Object.assign(Object.assign({}, this.state.model), { n: parseInt(event.target.value || null, null) }),
        });
    }
    /**
     * React to the number of workers changing.
     */
    onProfileChanged(event) {
        this.setState({
            model: Object.assign(Object.assign({}, this.state.model), { profile: event.target.value }),
        });
    }
    /**
     * React to the number of workers changing.
     */
    onClusterIdChanged(event) {
        this.setState({
            model: Object.assign(Object.assign({}, this.state.model), { cluster_id: event.target.value }),
        });
    }
    /**
     * Render the component..
     */
    render() {
        const model = this.state.model;
        // const disabledClass = "ipp-mod-disabled";
        return (React.createElement("div", null,
            React.createElement("div", { className: "ipp-DialogSection" },
                React.createElement("div", { className: "ipp-DialogSection-item" },
                    React.createElement("span", { className: `ipp-DialogSection-label` }, "Profile"),
                    React.createElement("input", { className: "ipp-DialogInput", value: model.profile, type: "string", placeholder: "default", onChange: (evt) => {
                            this.onProfileChanged(evt);
                        } })),
                React.createElement("div", { className: "ipp-DialogSection-item" },
                    React.createElement("span", { className: `ipp-DialogSection-label` }, "Cluster ID"),
                    React.createElement("input", { className: "ipp-DialogInput", value: model.cluster_id, type: "string", placeholder: "auto", onChange: (evt) => {
                            this.onClusterIdChanged(evt);
                        } })),
                React.createElement("div", { className: "ipp-DialogSection-item" },
                    React.createElement("span", { className: `ipp-DialogSection-label` }, "Engines"),
                    React.createElement("input", { className: "ipp-DialogInput", value: model.n, type: "number", step: "1", placeholder: "auto", onChange: (evt) => {
                            this.onScaleChanged(evt);
                        } })))));
    }
}
/**
 * Show a dialog for Dialog a cluster model.
 *
 * @param model: the initial model.
 *
 * @returns a promse that resolves with the user-selected Dialogs for the
 *   cluster model. If they pressed the cancel button, it resolves with
 *   the original model.
 */
export function newClusterDialog(model) {
    let updatedModel = Object.assign({}, model);
    const escapeHatch = (update) => {
        updatedModel = update;
    };
    return showDialog({
        title: `New Cluster`,
        body: React.createElement(NewCluster, { initialModel: model, stateEscapeHatch: escapeHatch }),
        buttons: [Dialog.cancelButton(), Dialog.okButton({ label: "CREATE" })],
    }).then((result) => {
        if (result.button.accept) {
            return updatedModel;
        }
        else {
            return null;
        }
    });
}
